.main {
	border-radius: 14px;
	background-color: #f4f6fa;
	padding: 20px 60px 70px 60px;
	margin-top: 90px;
	min-height: calc(100vh - 185px);
}

.head_main {
	position: fixed;
	right: 0px;
	top: 0px;
	z-index: 9;
	width: 100%;
}

.head_main .head_in {
	padding: 23px 0px;
	background: #fff;
}

.head_main .head_in .head_rht .logo {
	border-radius: 14px;
	box-shadow: 0 10px 20px 0 rgba(0,0,0,0.1);
	background-color: #fff;
	padding: 8.25px 13.5px;
	width: max-content;
	position: relative;
	cursor: pointer;
}

.head_main .head_in .head_rht .logo a:first-child {
	margin-left: 10px;
}

.head_main .head_in .head_rht .logo .menu_out {
	width: 190px;
	position: absolute;
	right: -5px;
	top: 0px;
	display: none;
